		/* 版本比较 */
		compareVersion(v1, v2) {
			v1 = v1.split('.')
			v2 = v2.split('.')
			const len = Math.max(v1.length, v2.length)

			while (v1.length < len) {
				v1.push('0')
			}
			while (v2.length < len) {
				v2.push('0')
			}

			for (let i = 0; i < len; i++) {
				const num1 = parseInt(v1[i])
				const num2 = parseInt(v2[i])

				if (num1 > num2) {
					return 1
				} else if (num1 < num2) {
					return -1
				}
			}

			return 0
		},
		/* 计算运动距离,单位为公里 */
		GetDistance(lat1, lng1, lat2, lng2) {
			console.log(lat1, lng1, lat2, lng2)
			var radLat1 = lat1 * Math.PI / 180.0;
			var radLat2 = lat2 * Math.PI / 180.0;
			var a = radLat1 - radLat2;
			var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0;
			var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
				Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)));
			s = s * 6378.137; // EARTH_RADIUS;
			s = Math.round(s * 10000) / 10000;
			return s;
		},
		/* 秒表 */
		coutDown() {
			this.cutDownTimer = setInterval(function() {

				let str_sec = self.n_sec;
				let str_min = self.n_min;
				let str_hour = self.n_hour;
				if (self.n_sec < 10) {
					str_sec = "0" + self.n_sec;
				}
				if (self.n_min < 10) {
					str_min = "0" + self.n_min;
				}

				if (self.n_hour < 10) {
					str_hour = "0" + self.n_hour;
				}

				let time = str_hour + ":" + str_min + ":" + str_sec;
				self.time = time
				self.n_sec++;
				if (self.n_sec > 59) {
					self.n_sec = 0;
					self.n_min++;
				}
				if (self.n_min > 59) {
					self.n_sec = 0;
					self.n_hour++;
				}

			}, 1000);
